import React from 'react'
import { BriefcaseBusiness } from 'lucide-react';
import Link from 'next/link';

const Navbar = () => {
  return (
    <div className='relative top-0 w-full bg-black'>
        <div className='py-5 px-3 flex justify-between items-center max-w-[90%]'>
            <div className="flex gap-3">
                <BriefcaseBusiness />
                <p>Job Portal</p>
            </div>

            <div className="flex gap-3">
                <Link href={"/"}>Home</Link>
                <Link href={"/"}>Jobs</Link>
                <Link href={"/"}>About Us</Link>
                <Link href={"/"}>Contact Us</Link>
            </div>

            <div className='flex gap-3 '>

            </div>
        </div>
    </div>
  )
}

export default Navbar